.maincontent {
  box-sizing: border-box;
  background-color: #f0f0f0;
}

.container {
  width: 660px;
  height: 100vh;
  max-width: 95%;
  margin: 0 auto;
}
.form {
  font-family: $font-family-roboto;
  display: flex;
  flex-direction: column;
  padding-top: 60px;
  

  &__title {
    margin: 20px 0 22px;
    font-size: 18px;
    font-weight: 600;
    color: #010101;
      
    @include mobile {
    text-align: center;
    }
  }

  &__skillometer-title {
    margin: 16px 0 15px;
    padding: 0 60px 0 22px;
    font-size: 18px;
    font-weight: 600;
    color: #010101;
              
    @include mobile {
      padding: 0;
      text-align: center;
    }
  }

  &__section {
    display: flex;
    flex-direction: column;
    background: url(../images/backgrong-form-section.jpg) center center / cover no-repeat;
    padding: 12px 22px 8px 22px;

    &:not(:last-child) {
      margin-bottom: 2px;
    }
  }

  &__intro {
    flex-direction: row;
    position: relative;
      
    @include mobile {
      flex-direction: column;
    }
  }

  &__skills {
    flex-direction: row;
    padding-top: 28px;
    padding-bottom: 55px;
              
    @include mobile {
      flex-direction: column;
    }
  }

  &__left {
    @include mobile {
    display: flex;
    justify-content: center;
  }
  }

  &__right {
    width: 80%;
    margin-top: 12px;
      
    @include mobile {
    flex-direction: column;
    width: 100%;
  }
  }

  &__col {
    &:not(:last-child) {
      margin-bottom: 26px;
    }
  }

  &__avatar-wrap {
    width: 138px;
    height: 140px;
    margin-top: 20px;
    margin-right: 22px;
  }

  &__input {
    width: 100%;
    font-size: 12px;
    font-weight: 600;
    letter-spacing: 1px;
    color: #666;
    border: transparent;
    background: transparent;
    border-bottom: 2px solid #333333;
    padding: 7px 0;
    
    &::placeholder {
      font-size: 12px;
      font-weight: 600;
      letter-spacing: 1px;
      color: #666;
      text-transform: uppercase;;
    }
  }
  &__approved {
    position: absolute;
    right: 1%;
    bottom: 5%;
    }

  &__count {
    padding-left: 16px;
              
    @include mobile {
      padding: 0;
    }
  }

  &__number{
    width: 100%;
    border-width: 0;
    font-size: 50px;
    font-weight: 900;
    background-color: transparent;
    color: #0093d7;
    transition: all 0.3s;
              
    @include mobile {
      text-align: center;
    }
  }
}

