.skillometer {
  &__block {
    height: 155px;
  }
  &__meter{
    display: block;
    width: 273px;
    height: 155px;
    margin: 0 auto;
  }

  &__left{
    transform: skewX(-2deg) skewY(1deg);
    stroke-width: 58;
    stroke-dashoffset: 253;
    stroke-dasharray: 120 400;
    stroke: #ffc815;
    fill: none;
  }
  
  &__center{
    stroke-width: 56;
    stroke-dashoffset: 128;
    stroke-dasharray: 120 400;
    stroke: #a3cd3b;
    fill: none;
  }
  
  &__right{
    transform: skewX(2deg) skewY(1deg);
    stroke-width: 58;
    stroke-dashoffset: 5;
    stroke-dasharray: 120 400;
    stroke: #0093d7;
    fill: none;
  }
  
  &__clock{
    transform: rotate(-90deg);
    transform-origin: 137px 146px;
    fill: #000;
    transition: all 0.3s;
  }
  
  .rang-slider {
    display: none;
  
  }
  &__circle{
    fill: #000;
  }
}




