.skills {
  &__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__item {
    width: 25%;
    padding-left: 28px;
    margin-bottom: 41px;
          
    @include mobile {
      width: 33.3%;
    }
  }

  &__checkbox {
    display: none;

    &:checked + .skills__skill::before {
      background-color: #0093d7;
    }

    &:checked + .skills__skill::after {
      opacity: 1;
    }
  }
  
  &__skill {
    position: relative;
    cursor: pointer;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 600;
    color: #666;

    &::before {
      content: "";
      position: absolute;
      left: -25px;
      top: -1px;
      width: 20px;
      height: 20px;
      background-color: #eee;
      border-radius: 5px;
      transition: all 0.2s;
    }

    &::after {
      content: "";
      position: absolute;
      left: -20px;
      top: 6px;
      transform: rotate(-45deg);
      width: 10px;
      height: 4px;
      border-bottom: 2px solid #fff;
      border-left: 2px solid #fff;
      opacity: 0;
      transition: all 0.2s;
    }
  }
}